.progress-bar-button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-bar-button-background {
    width: 100%;
    height: 100%;
    background-color: #1e1e22;
    top: 0;
    overflow: hidden;
}

.progress-bar-button-content {
    text-align: center;
    justify-content: center;
    color: #fff;
    font-size: 1.2rem;
    font-family: "ArialRounded", serif;
}

.progress-bar-button-inner {
    position: absolute;
    border-radius: 2rem;
}