.navigation-screen {
    position: relative;
    grid-column: 1;
    grid-row: 1;
    height: 100%;
    max-height: 100%;
    width: 100vw;
    transition: 0.2s;
}

.navigation-stack {
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    width: 100%;
    display: grid;
}