.loading-placeholder {
    background: #222222;
    text-align: center;
}

.l-rounded {
    border-radius: 1.5rem;
}

.l-margin {
    margin: 0.2rem;
}

.loading-balance-span {
    width: 20rem;
    height: 2.7rem;
}

.loading-telegram-name {
    width: 8rem;
    height: 1.5rem;
}

.loading-center {
    display: flex;
    text-align: center;
    justify-content: center;
    justify-items: center;
    width: 100%;
}

.loading-invite-friend-button {
    width: 100%;
    height: 3rem;
    margin: 0 0.5rem 0 1rem;
}

.loading-friends-claim-button {
    width: 100%;
    height: 3rem;
    margin: 0 1rem 0 0.5rem;
}

.loading-friends-board-header {
    height: 3rem;
    width: 14rem;
    margin: auto auto auto 1rem
}

.loading-button-subscript {
    height: 1.3rem;
    width: 20rem;
}
