.task-entry-done {
    background: none;
    border-color: #282828;
    border-style: solid;
    border-width: 1px;
    border-radius: 1rem;
}

.task-status-icon {
    display: flex;
    align-content: end;
}

.box-right {
    display: flex;
    align-items: center;
    justify-content: end;
    justify-items: center;
    width: 100%;
    height: 100%;
}

.task-name {
    margin: 0.2rem 0.2rem 0 0.2rem;
    font-size: 1rem;
    white-space: nowrap;
    width: 100%;
}

.spinning {
    animation: rotate-animation 1s infinite linear;
}

@keyframes rotate-animation {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.clickable {
    cursor: pointer;
}