body {
    --main-view-bg: #000;
    --font-bright: #fff;
}

.main-view {
    width: 100%;
    height: 100%;
    background-color: var(--main-view-bg);
    display: grid;
    grid-template-rows: 1fr 15rem 4fr 6rem;
    color: var(--font-bright);
}

.claim-button-outer {
    justify-content: center;
    text-align: center;
    height: 5rem;
}

.claim-button {
    margin: 0.2rem 1rem 0.2rem 1rem;
    width: calc(100% - 2rem);
    height: 3rem;
}

.balance-display{
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
}