.friends-view {
    background-color: #000000;
    height: 100%;
    display: grid;
    grid-template-rows: 4rem 1fr 6rem;
}

.friends-board-header {
    color: #ffffff;
    font-size: 1.6rem;
    display: flex;
    text-align: left;
    width: 100%;
    margin: 1rem auto auto 1rem;
}

.friends-view-buttons {
    margin: 0.2rem 0.2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}