body {
    --header-bg: #000;
    --font-bright: #fff;
    --font-dim: #aaa;
    --font-cancel: var(--font-bright);
}

.mini-app-header {
    display: grid;
    grid-template-columns: 10% 80% 10%;
    background-color: var(--header-bg);
    height: 5rem;
    width: 100%;
}

.header-cancel {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    text-align: left;
    color: var(--font-cancel);
    font-size: 1rem;
}

.header-name {
    overflow: hidden;
    white-space: nowrap;
    color: var(--font-bright);
    font-size: 1.2rem;
    margin: 0;
}

.header-bot-span {
    color: var(--font-dim);
    font-size: 0.8rem;
}

.header-center {
    text-align: center;
    justify-content: center;
}

.y-center {
    margin: auto;
    width: 50%;
}