.notification-text {
    color: #fff;
    font-family: "ArialRounded", serif;
}

.notification {
    background-color: #282828;
    border-radius: 1.5rem;
    height: 3rem;
    width: calc(100% - 2rem);
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.2rem 1rem 0.2rem 1rem;
    position: relative;
}

.notification-started {
    opacity: 0;
    top: -4rem;
}

.notification-showing {
    opacity: 1;
    top: 0;
    transition: 1s;
}

.notification-hidden {
    opacity: 0;
    height: 3rem;
    transition: 1s;
    margin: 0.1rem 1rem 0.1rem 1rem;
}

.notification-gone {
    height: 0;
    opacity: 0;
    margin: 0 1rem 0 1rem;
    transition: 0.3s;
}