.friend-entry {
    background-color: #212121;
    display: grid;
    grid-template-columns: 5rem 1fr 1fr;
    gap: 0.3rem;
    border-radius: 1rem;
    width: calc(100% - 2rem);
    height: 4.5rem;
    margin: 0.4rem 1rem 0.4rem 1rem;
    color: #ffffff;
}

.friend-name-bonus-union{
    text-align: left;
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: 0.2rem;
}

.friend-picture {
    width: 3rem;
    height: 3rem;
    margin: auto;
    border-radius: 50%;
}

.friend-balance {
    margin: auto 1rem auto auto;
    text-align: right;
    font-size: 1.2rem;
    font-family: "ArialRounded", serif;
    white-space: nowrap;
    width: 100%;
}

.friend-nickname {
    margin: 0.2rem 0.2rem 0 0.2rem;
    font-size: 1rem;
    font-family: "ArialRounded", serif;
    white-space: nowrap;
    width: 100%;
}

.box-center {
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    width: 100%;
    height: 100%;
}

.box-down {
    display: flex;
    align-items: end;
    justify-content: center;
    justify-items: end;
    width: 100%;
    height: 100%;
}

.friend-bonus {
    margin: 0.2rem;
    font-size: 0.85rem;
    font-family: "ArialRounded", serif;
}

.bonus-gray {
    color: #808080;
}

.bonus-blue {
    color: #207bf9;
}