.mini-app-navigation {
    width: 100%;
    justify-content: center;
    text-align: center;
    background-color: #000;
    display: grid;
    grid-template-columns: 1fr 6fr 1fr;
}

.navigation-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    font-size: 2rem;
    margin: auto;
    width: 100%;
}

.nav-button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: #fff;
    height: 4rem;
    display: flex;
    justify-content: center;
    justify-items: center;
    text-align: center;
}