.invite-friend-tip-outer {
    background: none;
    border-color: #282828;
    border-style: solid;
    border-width: 1px;
    border-radius: 1rem;
    width: calc(100% - 2rem);
    height: 4rem;
    margin: 0.4rem 1rem 0.4rem 1rem;
    color: #707070;
    display: flex;
    align-items: center;
    align-content: center;
}

.invite-friend-tip-span {
    margin: auto;
    font-size: 1.1rem;
    font-family: "Arial", serif;
}

.friend-list{
    object-fit: cover;
    overflow-y: scroll;
    overflow-x: hidden;
    height: calc(100vh - 20rem);
}