.auth-view {
    width: 100%;
    height: 100%;
    background: #000;
    color: white;
    display: grid;
    grid-template-rows: 1fr 6rem 2fr 3rem;
}

.auth-text {
    color: #808080;
    font-size: 1.2rem;
    display: inline-block;
}

.auth-data {
    display: grid;
    margin: 1rem;
}

.bg-black {
    width: 100%;
    height: 100%;
    background-color: #000;
}

.register-button {
    margin: 0.2em 1em 0.2em 1em;
}